.elaboration_left {
  position: relative;
  height: 100%;
  width: 100%;
  padding: 280px 0 0 320px;
  box-sizing: border-box;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  background-color: rgb(208, 226, 226);
}
.elaboration_conteiner_title {
  margin: 0 0 80px 0;
  font-size: 1.8rem;
}
.border {
  position: absolute;
  border: 2px solid #3f3f3f;
  border-right: none;
  height: 190px;
  width: 570px;
  transform: translateY(-185px);
}
.elaboration_conteiner_lists {
  display: flex;
  flex-direction: column;
  gap: 8px;
  font-size: 1.2rem;
}
.liaison {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 100px;
  width: 100%;
  background-color: rgb(69, 231, 210);
}
.communication {
  margin: 0;
  text-align: end;
  font-size: 42px;
  color: rgb(255, 255, 255);
  transform: translateY(20px);
}
